h1 {
  margin: 0 0 1.4rem;
}

.ImageClassifier {
  padding: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

// .ImageClassifier > div:not(.webcam-container) {
